.profile-page {
  max-width: 480px;
}

.profile-card {
  padding: 24px;
}

.profile-form {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.profile-msg {
  padding: 10px 12px;
  border-radius: var(--radius-sm);
  font-size: 0.9rem;
}

.profile-msg.success {
  background: #ecfdf5;
  color: #059669;
}

.profile-msg.error {
  background: #fef2f2;
  color: #dc2626;
}

.profile-form .input:disabled {
  background: #f1f5f9;
  color: var(--text-muted);
}

.profile-readonly {
  padding: 10px 14px;
  background: #f8fafc;
  border: 1px solid var(--border);
  border-radius: var(--radius-sm);
  color: var(--text);
  font-size: 0.95rem;
}
