.home-page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.home-nav {
  background: var(--card);
  border-bottom: 1px solid var(--border);
  padding: 16px 0;
}

.home-nav-inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.home-brand {
  display: flex;
  align-items: center;
  gap: 12px;
}

.home-logo {
  font-size: 1.75rem;
  color: var(--primary);
}

.home-app-name {
  font-weight: 700;
  font-size: 1.15rem;
  color: var(--text);
}

.home-actions {
  display: flex;
  gap: 12px;
}

.hero {
  flex: 1;
  display: flex;
  align-items: center;
  padding: 60px 0;
  background: linear-gradient(180deg, #eff6ff 0%, var(--bg) 100%);
}

.hero-inner {
  text-align: center;
  max-width: 640px;
}

.hero h1 {
  font-size: 2.25rem;
  font-weight: 700;
  color: var(--text);
  margin-bottom: 16px;
  line-height: 1.2;
}

.hero-desc {
  font-size: 1.1rem;
  color: var(--text-muted);
  margin-bottom: 28px;
  line-height: 1.6;
}

.hero-buttons {
  display: flex;
  gap: 16px;
  justify-content: center;
  flex-wrap: wrap;
}

.btn-lg {
  padding: 14px 28px;
  font-size: 1rem;
}

.features {
  padding: 60px 0;
}

.features h2 {
  font-size: 1.75rem;
  font-weight: 700;
  text-align: center;
  margin-bottom: 32px;
  color: var(--text);
}

.feature-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 24px;
}

.feature-card {
  padding: 24px;
}

.feature-card h3 {
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 8px;
  color: var(--text);
}

.feature-card p {
  font-size: 0.95rem;
  color: var(--text-muted);
  line-height: 1.5;
}

.home-footer {
  padding: 24px 0;
  border-top: 1px solid var(--border);
  text-align: center;
  color: var(--text-muted);
  font-size: 0.9rem;
}
