.page-loading {
  padding: 24px;
  color: var(--text-muted);
}

.page-title {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 4px;
  color: var(--text);
}

.page-desc {
  color: var(--text-muted);
  margin-bottom: 24px;
  font-size: 0.95rem;
}

.assessments-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 20px;
}

.assessment-card {
  padding: 20px;
}

.assessment-card h3 {
  font-size: 1.05rem;
  font-weight: 600;
  margin-bottom: 6px;
  color: var(--text);
}

.assessment-meta {
  font-size: 0.9rem;
  color: var(--text-muted);
  margin-bottom: 4px;
}

.assessment-questions {
  font-size: 0.85rem;
  color: var(--text-muted);
  margin-bottom: 16px;
}

.btn-sm {
  padding: 8px 16px;
  font-size: 0.9rem;
}

.no-data-block {
  grid-column: 1 / -1;
  padding: 20px;
  text-align: center;
}

.no-data {
  color: var(--text);
  font-weight: 500;
  margin-bottom: 8px;
}

.no-data-hint {
  color: var(--text-muted);
  font-size: 0.9rem;
  max-width: 420px;
  margin: 0 auto;
}
