.question-form-card {
  padding: 24px;
  margin: 20px 0;
  max-width: 560px;
}

.question-form-card form {
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.question-form-card textarea {
  resize: vertical;
  min-height: 80px;
}

.questions-list {
  padding: 20px;
  margin-top: 24px;
}

.questions-list h2 {
  font-size: 1.1rem;
  margin-bottom: 16px;
  color: var(--text);
}

.questions-list ul {
  list-style: none;
}

.question-item {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 16px;
  padding: 14px 0;
  border-bottom: 1px solid var(--border);
}

.question-item:last-child {
  border-bottom: none;
}

.q-text {
  font-size: 0.95rem;
  color: var(--text);
  margin-bottom: 4px;
}

.q-meta {
  font-size: 0.8rem;
  color: var(--text-muted);
}
