.reports-table-wrap {
  padding: 0;
  overflow: auto;
}

.reports-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 0.95rem;
}

.reports-table th,
.reports-table td {
  padding: 14px 20px;
  text-align: left;
  border-bottom: 1px solid var(--border);
}

.reports-table th {
  background: #f8fafc;
  font-weight: 600;
  color: var(--text);
}

.reports-table tr:hover td {
  background: #f8fafc;
}

.reports-table .no-data {
  padding: 24px;
  color: var(--text-muted);
}
