.dashboard-loading,
.chart-empty {
  color: var(--text-muted);
  padding: 24px;
  text-align: center;
}

.student-dashboard {
  max-width: 1100px;
  margin: 0 auto;
}

.dashboard-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text);
  margin-bottom: 4px;
}

.dashboard-subtitle {
  font-size: 0.95rem;
  color: var(--text-muted);
  margin-bottom: 24px;
}

.skill-cards {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
  gap: 20px;
  margin-bottom: 24px;
}

.skill-card {
  padding: 20px;
  border-top: 4px solid var(--primary);
}

.skill-card-title {
  font-size: 0.9rem;
  font-weight: 600;
  color: var(--text);
  margin-bottom: 8px;
}

.skill-card-value {
  font-size: 1.75rem;
  font-weight: 700;
  color: var(--text);
  margin-bottom: 10px;
}

.skill-card-bar {
  height: 6px;
  background: var(--border);
  border-radius: 3px;
  overflow: hidden;
}

.skill-card-fill {
  height: 100%;
  border-radius: 3px;
  transition: width 0.3s ease;
}

.take-assessment {
  padding: 20px;
  margin-bottom: 24px;
}

.take-assessment h2,
.chart-card h2,
.recent-results h2 {
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 16px;
  color: var(--text);
}

.upcoming-test {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
  flex-wrap: wrap;
}

.upcoming-meta {
  font-size: 0.85rem;
  color: var(--text-muted);
  margin-top: 4px;
}

.no-upcoming,
.no-results {
  color: var(--text-muted);
  font-size: 0.95rem;
}

.dashboard-charts {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 24px;
  margin-bottom: 24px;
}

@media (max-width: 900px) {
  .dashboard-charts {
    grid-template-columns: 1fr;
  }
}

.chart-card {
  padding: 20px;
}

.chart-wrap {
  height: 260px;
}

.recent-results {
  padding: 20px;
}

.results-list {
  list-style: none;
}

.result-item {
  display: grid;
  grid-template-columns: 1fr 1fr auto auto;
  gap: 16px;
  align-items: center;
  padding: 12px 0;
  border-bottom: 1px solid var(--border);
  font-size: 0.9rem;
}

.result-item:last-child {
  border-bottom: none;
}

.result-subject {
  color: var(--text-muted);
  font-weight: 500;
}

.result-title {
  color: var(--text);
}

.result-score {
  font-weight: 700;
  color: var(--primary);
}

.result-date {
  color: var(--text-muted);
  font-size: 0.85rem;
}

@media (max-width: 600px) {
  .result-item {
    grid-template-columns: 1fr auto;
  }
  .result-subject { grid-column: 1; }
  .result-title { grid-column: 1; }
  .result-score { grid-column: 2; grid-row: 1; }
  .result-date { grid-column: 2; grid-row: 2; }
}
