.take-test {
  max-width: 720px;
  margin: 0 auto;
}

.test-header {
  padding: 20px;
  margin-bottom: 20px;
}

.test-header h1 {
  font-size: 1.25rem;
  font-weight: 700;
  margin-bottom: 4px;
  color: var(--text);
}

.test-header p {
  font-size: 0.9rem;
  color: var(--text-muted);
  margin-bottom: 12px;
}

.test-progress {
  font-size: 0.85rem;
  color: var(--text-muted);
}

.test-timer {
  font-size: 1.1rem;
  font-weight: 700;
  color: var(--primary);
  background: #eff6ff;
  padding: 6px 12px;
  border-radius: var(--radius-sm);
  display: inline-block;
}

.test-timer.timer-warning {
  color: #ef4444;
  background: #fef2f2;
}

.finish-btn {
  background-color: var(--primary);
  color: white;
  width: 100%;
}

.test-msg {
  padding: 12px;
  border-radius: var(--radius-sm);
  margin-bottom: 20px;
  text-align: center;
  font-weight: 500;
}

.test-msg.success {
  background-color: #f0fdf4;
  color: #166534;
  border: 1px solid #bbf7d0;
}

.test-body {
  padding: 24px;
  margin-bottom: 20px;
}

.question-text {
  font-size: 1.1rem;
  font-weight: 600;
  color: var(--text);
  margin-bottom: 8px;
  line-height: 1.5;
}

.question-difficulty {
  font-size: 0.8rem;
  color: var(--text-muted);
  margin-bottom: 20px;
}

.options-list {
  list-style: none;
}

.options-list li {
  margin-bottom: 10px;
}

.option-label {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  padding: 14px 16px;
  border: 1px solid var(--border);
  border-radius: var(--radius-sm);
  cursor: pointer;
  transition: border-color 0.2s, background 0.2s;
}

.option-label:hover {
  border-color: var(--primary);
  background: #eff6ff;
}

.option-label input {
  margin-top: 3px;
  accent-color: var(--primary);
}

.option-label input:checked+span {
  font-weight: 600;
}

.option-label:has(input:checked) {
  border-color: var(--primary);
  background: #eff6ff;
}

.test-nav {
  display: flex;
  justify-content: space-between;
  margin-top: 24px;
  padding-top: 20px;
  border-top: 1px solid var(--border);
}

.test-quick-nav {
  padding: 16px;
}

.test-quick-nav p {
  font-size: 0.9rem;
  margin-bottom: 12px;
  color: var(--text-muted);
}

.quick-dots {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.quick-dot {
  width: 36px;
  height: 36px;
  border-radius: var(--radius-sm);
  border: 1px solid var(--border);
  background: var(--card);
  font-size: 0.9rem;
  font-weight: 500;
  color: var(--text);
}

.quick-dot:hover {
  border-color: var(--primary);
  background: #eff6ff;
}

.quick-dot.active {
  background: var(--primary);
  border-color: var(--primary);
  color: white;
}

.quick-dot.answered {
  border-color: var(--success);
  color: var(--success);
}

.no-options {
  color: var(--text-muted);
  font-style: italic;
  padding: 16px;
  text-align: center;
}

.take-test-error {
  max-width: 500px;
  margin: 40px auto;
}