.loading-screen {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  color: var(--text-muted);
}

.dashboard-layout {
  display: flex;
  min-height: 100vh;
}

.sidebar {
  width: 260px;
  background: var(--sidebar-bg);
  color: var(--sidebar-text);
  display: flex;
  flex-direction: column;
  transition: width 0.2s;
  flex-shrink: 0;
}

.sidebar.closed {
  width: 72px;
}

.sidebar.closed .sidebar-title,
.sidebar.closed .sidebar-text {
  opacity: 0;
  width: 0;
  overflow: hidden;
  padding: 0;
  margin: 0;
  pointer-events: none;
}

.sidebar-icon {
  font-size: 1.25rem;
  flex-shrink: 0;
  width: 24px;
  display: flex;
  justify-content: center;
}

.sidebar-brand {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 24px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.08);
  min-height: 72px;
}

.sidebar-logo {
  font-size: 1.5rem;
  color: var(--accent);
  flex-shrink: 0;
}

.sidebar-title {
  font-weight: 700;
  font-size: 1rem;
  white-space: nowrap;
  transition: opacity 0.2s;
}

.sidebar-text {
  white-space: nowrap;
  transition: opacity 0.2s, width 0.2s;
  overflow: hidden;
}

.sidebar-nav {
  padding: 16px 12px;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.sidebar-link {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px 16px;
  border-radius: var(--radius-sm);
  color: var(--sidebar-text);
  font-weight: 500;
  transition: background 0.2s, color 0.2s;
}

.sidebar-link:hover {
  background: rgba(255, 255, 255, 0.08);
  color: white;
}

.sidebar-link.active {
  background: var(--primary);
  color: white;
}

.main-wrap {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-width: 0;
}

.dashboard-header {
  height: 64px;
  background: var(--card);
  border-bottom: 1px solid var(--border);
  display: flex;
  align-items: center;
  padding: 0 24px;
  gap: 16px;
}

.menu-btn {
  background: none;
  border: none;
  font-size: 1.25rem;
  color: var(--text);
  padding: 8px;
}

.header-spacer {
  flex: 1;
}

.dashboard-main {
  flex: 1;
  padding: 24px;
  overflow: auto;
}

@media (max-width: 768px) {
  .sidebar {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    z-index: 1000;
    width: 260px;
    box-shadow: 4px 0 12px rgba(0, 0, 0, 0.1);
  }

  .sidebar.closed {
    transform: translateX(-100%);
    width: 260px;
  }

  .dashboard-main {
    padding: 16px;
  }

  .main-wrap {
    margin-left: 0;
  }
}